// --- COMPONENTS ---

function SectionLabel({ number, title }: { number: string; title: string }) {
  return (
    <div className="flex items-baseline gap-4 mb-12 border-b border-border pb-6">
      {/* Accent Color for the number only */}
      <span className="text-sm font-mono font-bold text-muted-foreground">
        {number}
      </span>
      <h2 className="text-3xl md:text-4xl font-bold tracking-tight text-foreground">
        {title}
      </h2>
    </div>
  );
}

function StatBox({
  label,
  value,
  icon,
}: {
  label: string;
  value: string;
  icon: any;
}) {
  return (
    <div className="bg-card p-8 flex flex-col justify-between h-40 hover:bg-muted/30 transition-colors group">
      <div className="flex justify-between items-start">
        <span className="text-xs font-bold uppercase tracking-widest text-muted-foreground group-hover:text-foreground transition-colors">
          {label}
        </span>
        <span className="text-muted-foreground/50 group-hover:text-accent transition-colors">
          {icon}
        </span>
      </div>
      <span className="text-2xl md:text-3xl font-bold text-foreground tracking-tight">
        {value}
      </span>
    </div>
  );
}

function CurriculumTabs({ curriculum }: { curriculum: any[] }) {
  const [activeTab, setActiveTab] = useState(0);

  return (
    <div className="bg-card rounded-[2.5rem] p-2 md:p-4 border border-border">
      {/* Tab Navigation - Cleaner, less colorful */}
      <div className="flex flex-wrap gap-2 mb-8 p-2">
        {curriculum.map((year, idx) => (
          <button
            key={idx}
            onClick={() => setActiveTab(idx)}
            className={cn(
              "px-6 py-3 rounded-full text-sm font-bold uppercase tracking-wider transition-all duration-300 border",
              activeTab === idx
                ? // Accent Color only for active text/border, background remains light
                  "bg-primary/10 text-primary border-primary/20"
                : "bg-transparent text-muted-foreground border-transparent hover:bg-muted"
            )}
          >
            {year.year}
          </button>
        ))}
      </div>

      {/* Tab Content */}
      <div className="relative min-h-[300px]">
        <AnimatePresence mode="wait">
          <motion.div
            key={activeTab}
            initial={{ opacity: 0, y: 10, filter: "blur(4px)" }}
            animate={{ opacity: 1, y: 0, filter: "blur(0px)" }}
            exit={{ opacity: 0, y: -10, filter: "blur(4px)" }}
            transition={{ duration: 0.3 }}
            className="p-4 md:p-8"
          >
            <div className="flex flex-col md:flex-row gap-8 md:items-end mb-12">
              <div>
                <div className="text-xs font-bold text-primary uppercase tracking-widest mb-2">
                  Focus Area
                </div>
                <h3 className="text-3xl md:text-5xl font-bold text-foreground tracking-tight">
                  {curriculum[activeTab].focus}
                </h3>
              </div>
              <div className="h-px flex-1 bg-border mb-4 hidden md:block" />
            </div>

            <div className="grid grid-cols-1 sm:grid-cols-2 gap-4">
              {curriculum[activeTab].subjects.map(
                (subject: string, idx: number) => (
                  <motion.div
                    key={idx}
                    initial={{ opacity: 0, x: -10 }}
                    animate={{ opacity: 1, x: 0 }}
                    transition={{ delay: idx * 0.1 }}
                    className="group flex items-center gap-4 p-6 rounded-2xl bg-background border border-border hover:border-accent/50 hover:shadow-sm transition-all duration-300"
                  >
                    {/* Number Circle */}
                    <div className="w-10 h-10 rounded-full bg-muted border border-border flex items-center justify-center group-hover:bg-accent group-hover:border-accent transition-colors duration-300">
                      <span className="text-xs font-bold text-muted-foreground group-hover:text-accent-foreground transition-colors">
                        {(idx + 1).toString().padStart(2, "0")}
                      </span>
                    </div>
                    <span className="font-bold text-foreground text-lg">
                      {subject}
                    </span>
                  </motion.div>
                )
              )}
            </div>
          </motion.div>
        </AnimatePresence>
      </div>
    </div>
  );
}
